import React from "react";
import "./InFocusSection.scss";
import { LazyLoadImage } from "react-lazy-load-image-component";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";

export const InFocusSection = (props) => {
  const inFocusContent = props.data.cardsCollection.items;
  return (
    <div className="in-focus-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="content-container">
              <div className="title-wrapper text-center mb-30">
                <span className="text">
                  in <b>focus</b>
                </span>
              </div>
            </div>
            <div className="in-focus-container">
              <div className="in-focus-row">
                <OwlCarousel 
                loop={false} 
                nav={true}
                responsive={
                    {
                        "0": {
                            items: 1
                        },
                        "768": {
                            items: 2
                        },
                        "992": {
                            items: 3
                        },
                    }
                }>
                  {inFocusContent.map((item, idx) => (
                    <div key={"inFocus" + (idx + 1)} className="in-focus-col">
                      <div className="in-focus-card">
                        <div className="thumbnail">
                          <LazyLoadImage  effect="opacity"
                            src={item.thumbnail.url}
                            alt="Muvin"
                            className="img-fluid"
                          />
                        </div>
                        <div className="in-focus-body">
                          <div className="source-thumbnail">
                            <LazyLoadImage  effect="opacity"
                              src={item.sourceThumbnail.url}
                              alt="Muvin"
                              className="img-fluid"
                            />
                          </div>
                          <div className="title">{item.title}</div>
                          <div className="description">{item.description}</div>
                          <div className="button-wrapper">
                            <a
                              className="btn btn-link btn-blue"
                              href={item.articleLink}
                              target="_blank"
                              rel="noreferrer"
                            >
                              read more
                            </a>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </OwlCarousel>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
