import React, { useState, useEffect } from "react";
import "./PartnerPage.scss";
import { LazyLoadImage } from "react-lazy-load-image-component";
import Banner from "../../banner/Banner";
import Loader from "../loader/Loader";
import ChatWithUs from "../chat-with-us/ChatWithUs";
import { getData, getImage, getStaticDataFunc, getWindowWidth, setStaticDataFunc } from "../../../common-service";

const query = `{
    page(id: "5OBNMVNww0c757e0q56c9G") {
      assetsCollection {
        items {
          fileName
          url
        }
      }
      articlesCollection(limit: 50) {
        items {
          ... on Section {
            sectionLabel
            titleMain
            titleSub
            description
            imagesMobileCollection(limit: 50) {
              items {
                url
              }
            }
            imagesCollection(limit: 50) {
              items {
                url
              }
            }
          }
        }
      }
    }
  }
  `;
export const PartnerPage = () => {
  const [page, setPage] = useState(null);
  let [isMobileState, setisMobileState] = useState(getWindowWidth);

  useEffect(() => {
    function handleResize() {
      setisMobileState(getWindowWidth);
    }

    setisMobileState(getWindowWidth);

    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  useEffect(() => {
    // Data call to get the dynamic content
    getData(query).then((res) => {
        setStaticDataFunc(
          res.data.data.page.assetsCollection.items
        );
        setPage(res.data.data.page.articlesCollection.items);
      })
      .catch((err) => {
        console.error("Static content", err);
      });
  }, []);

  if (!page || getStaticDataFunc().length === 0) {
    return <Loader></Loader>;
  }
  return (
    <div className="w-100">
      <Banner
        data={page[0]}
        showGateway={false}
        isMobileState={isMobileState}
        bannerClass={"partners-banner"}
      ></Banner>
      {/* Partners with us section */}
      <div className="why-parner-with-us-section">
        <div className="container">
          <div className="row">
            <div className="col-12 col-md-5">
              <div className="phone-wrapper">
                <LazyLoadImage  effect="opacity" src={isMobileState ? getImage('why-partner-with-us-mobile.png') : getImage('why-partner-with-us.png')} alt="Muvin" className="img-fluid" />
              </div>
            </div>
            <div className="col-12 col-md-7">
              <div className="content-container">
                <div className="title-wrapper">
                  <span className="text">
                    why <b>partner with us</b>
                  </span>
                </div>
                <div className="content-wrapper isList">
                  <ul>
                    <li className="isListIcon mb-lg-60">
                      <div className="icon">
                        <LazyLoadImage  effect="opacity" src={getImage('saving-goals-icon.png')} alt="Muvin" />
                      </div>
                      <div className="text">
                      muvin helps you meet your financial goals through goal setting
                      </div>
                    </li>
                    <li className="isListIcon mb-lg-60">
                      <div className="icon">
                        <LazyLoadImage  effect="opacity" src={getImage('view-card-icon.png')} alt="Muvin" />
                      </div>
                      <div className="text">
                      Use the muvin card to make purchases, both in-store and online 
                      </div>
                    </li>
                    <li className="isListIcon mb-lg-60">
                      <div className="icon">
                        <LazyLoadImage  effect="opacity" src={getImage('send-money-icon.png')} alt="Muvin" />
                      </div>
                      <div className="text">
                      Earn money on your savings through customisable interest rates
                      </div>
                    </li>
                  </ul>
                </div>
                <div className="button-wrapper">
                  <a href="/" className="btn btn-blue">
                    contact us
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Account Setup */}
      <div className="easy-account-wrapper partners-page">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="content-container">
                <div
                  className={
                    "title-wrapper mb-50 " +
                    (!isMobileState ? "text-center" : "")
                  }
                >
                  <span className="text">
                    partnership {isMobileState ? <br /> : ""}{" "}
                    <b>opportunities</b>
                  </span>
                </div>
              </div>
            </div>
            <div className="col-12 easy-account-steps-wrapper mb-30">
              <div className="row">
                <div className="col-12 col-md-4 easy-account-steps-col">
                  <div className="easy-account-steps-item">
                    <div className="image">
                      <LazyLoadImage  effect="opacity" src={getImage('donwload-acc-icon.svg')} alt="Muvin" />
                    </div>
                    <div className="content-body">
                      <div className="title">
                        <span>advertise </span>
                      </div>
                      <div className="description">
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                        sed do eiusmod tempor incididunt ut labore et dolore
                        magna.
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-12 col-md-4 easy-account-steps-col">
                  <div className="easy-account-steps-item">
                    <div className="image">
                      <LazyLoadImage  effect="opacity" src={getImage('submit-kyc.svg')} alt="Muvin" />
                    </div>
                    <div className="content-body">
                      <div className="title">
                        <span>offer discounts</span>
                      </div>
                      <div className="description">
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                        sed do eiusmod tempor incididunt ut labore et dolore
                        magna.
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-12 col-md-4 easy-account-steps-col">
                  <div className="easy-account-steps-item">
                    <div className="image">
                      <LazyLoadImage  effect="opacity" src={getImage('invite-your-child.svg')} alt="Muvin" />
                    </div>
                    <div className="content-body">
                      <div className="title">
                        <span>engage </span>
                      </div>
                      <div className="description">
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                        sed do eiusmod tempor incididunt ut labore et dolore
                        magna.
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="button-wrapper d-flex justify-content-center">
                <a href="/" className="btn btn-blue">
                  contact us
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* Account Setup */}

      {/* Partnership Section */}
      <div className="partnership-types-section">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="content-container">
                <div
                  className={
                    "title-wrapper mb-50 " +
                    (!isMobileState ? "text-center" : "")
                  }
                >
                  <span className="text">
                    our partnership {isMobileState ? <br /> : ""}
                    <b>types</b>
                  </span>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="partnership-types-image">
                {!isMobileState ? (
                  <LazyLoadImage  effect="opacity"
                    src={page[1].imagesCollection.items[0].url}
                    alt="Muvin"
                    className="img-fluid"
                  />
                ) : (
                  <LazyLoadImage  effect="opacity"
                    src={page[1].imagesMobileCollection.items[0].url}
                    alt="Muvin"
                    className="img-fluid"
                  />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      <ChatWithUs isMobileState={isMobileState}></ChatWithUs>
    </div>
  );
};
