import React from "react";
import "./PricingSection.scss";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { getImage } from "../../common-service";

const PricingSection = (props) => {
  const prices = [
    {
      title: "basic",
      price: 0,
      iconUrl: getImage('basic.svg'),
      description: "Design your own card",
      link: "/",
    },
    {
      title: "premium",
      price: 500,
      iconUrl: getImage('premium.svg'),
      description: "Design your own card and get your own card with your name",
      link: "/",
    },
    {
      title: "platinum",
      price: 1000,
      iconUrl: getImage('platinum.svg'),
      description: "Refer minumum 5 of your friends to get access",
      link: "/",
    },
  ];
  const isMobileState = props.isMobileState;

  return (
    <div className="pricing-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="content-container">
              <div className={"title-wrapper  " + (!isMobileState ? "text-center" : "")}>
                <span className="text">
                  our <b>pricing</b>
                </span>
              </div>
              <div className="card-wrapper pricing-cards">
                <div className="row">
                  {prices.map((price, idx) => (
                    <div key={"plan" + (idx + 1)} className="col-12 col-md-4">
                      <div
                        className={
                          "card-wrapper-item " +
                          (idx === 1 && !isMobileState ? "isBorder" : "")
                        }
                      >
                        <div className="card-header-container">
                          <div className="card-title">{price.title}</div>
                          <div className="price">
                            {price.price !== 0 && <span>&#8377;</span>}
                            {price.price === 0 ? "free" : price.price}
                          </div>
                        </div>
                        <div className="icon">
                          <LazyLoadImage effect="opacity"
                            src={price.iconUrl}
                            alt="Muvin"
                            className="img-fluid"
                          />
                        </div>
                        <div className="description text-center">
                          {price.description}
                        </div>
                        {price.link !== null && (
                          <div className="button-wrapper">
                            <a
                              href={price.link}
                              className="btn btn-link btn-blue"
                            >
                              view more
                            </a>
                          </div>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              {/* Only Mobile Display */}
              {isMobileState && (
                <div className="apps-button-wrapper button-wrapper isLink">
                  <a
                    href="/"
                    className={
                      "btn btn-app btn-image btn-left-img btn-center-img " +
                      (isMobileState ? "btn-blue btn-black-text" : "")
                    }
                  >
                    <LazyLoadImage effect="opacity" src={getImage('play-store-icon.svg')} alt="Muvin" />
                    <span>download the app</span>
                  </a>
                  <a
                    href="/"
                    className={
                      "btn btn-app btn-image btn-left-img btn-center-img " +
                      (isMobileState ? "btn-blue btn-black-text" : "")
                    }
                  >
                    <LazyLoadImage effect="opacity" src={getImage('app-store.svg')} alt="Muvin" />
                    <span>download the app</span>
                  </a>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PricingSection;
