import React, { useContext } from "react";
import './FaqSection.scss';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { Accordion, AccordionContext, Card, useAccordionToggle } from "react-bootstrap";
import { getImage } from "../../common-service";


const FaqSection = (props) => {
  const testimonials = props.data.cardsCollection.items;
  const isMobileState = props.isMobileState;

  function ContextAwareToggle({ children, eventKey, callback }) {
    const currentEventKey = useContext(AccordionContext);
  
    const decoratedOnClick = useAccordionToggle(
      eventKey,
      () => callback && callback(eventKey),
    );
  
    const isCurrentEventKey = currentEventKey === eventKey;
  
    return (
        <div
        className={"faq-button " + (isCurrentEventKey ? "isOpen" : "")}
      >
        <LazyLoadImage effect="opacity"
          src={isCurrentEventKey ? getImage('accordion-trigger-up.png') : getImage('accordion-trigger-down.png')}
          alt="Muvin"
          className="img-fluid"
          onClick={decoratedOnClick}
        />
      </div>
    );
  }

  return (
    <div className="faq-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="content-container">
              <div className="title-wrapper isIcon inline mb-10">
                <span className="text">
                  <b>faqs</b>
                </span>
                <span className="icon">
                  <LazyLoadImage effect="opacity" src={getImage('purple3-character.png')} alt="Muvin" />
                </span>
              </div>
              <div className="faq-accordion">
                <Accordion defaultActiveKey={isMobileState ? 0 : 1}>
                  {testimonials.map((testimonial, idx) => (
                    <Card key={"faqs " + (idx + 1)}>
                      <Card.Header>
                          <div className="faq-title">{testimonial.query}</div>
                          <ContextAwareToggle eventKey={idx + 1}>Click me!</ContextAwareToggle>
                        <Accordion.Toggle as={Card.Link} eventKey={idx + 1}>
                          
                        </Accordion.Toggle>
                      </Card.Header>
                      <Accordion.Collapse eventKey={idx + 1}>
                        <Card.Body>
                            {testimonial.description}
                        </Card.Body>
                      </Accordion.Collapse>
                    </Card>
                  ))}
                </Accordion>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FaqSection;
