import React from "react";
import "./SocialSection.scss";
import { LazyLoadImage } from 'react-lazy-load-image-component';

const SocialSection = (props) => {
  const posts = props.data.imagesCollection.items;
  const isMobileState = props.isMobileState;
  return (
    <div className="social-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="content-container">
              <div className="title-wrapper isIcon inline mb-10">
                <span className="text">
                  muvin <b>social</b>
                </span>
              </div>
              <div className="content-wrapper wid-md-70 wid-lg-70 mb-30">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                eiusmod tempor incididunt ut labore et dolore magna aliqua.
              </div>
              <div className="posts-thumbnail-wrapper mb-30">
                <div className="row posts-thumbnail-item-row">
                  {posts.map((post, idx) => (
                    <div key={"post" + (idx + 1)} className="col-12 col-sm-3 posts-thumbnail-item-col">
                      <div className="posts-thumbnail-item">
                        <LazyLoadImage effect="opacity" src={post.url} alt="Muvin" className="img-fluid" />
                      </div>
                    </div>
                  ))}
                </div>
              </div>
              <div
                className={"button-wrapper " + (!isMobileState ? "justify-content-center" : "")}
              >
                <a href="/" className={"btn btn-blue " + (isMobileState ? "w-100" : "")}>
                  follow us on instagram
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SocialSection;
