import React, { useState, useEffect } from "react";
import "./KeyFeatureItem.scss";

const KeyFeatureItem = (props) => {
  const featureData = props.data;
  const [hoverState, sethoverState] = useState(null);

  useEffect(() => {
    if (hoverState !== null) {
      props.target.current
        .classList.add("image-out");
      setTimeout(() => {
        props.target.current.src = hoverState.url;
        props.target.current

          .classList.remove("image-out");
        props.target.current

          .classList.add("image-in");
      }, 500);
    } else {
      props.target.current
        .classList.remove("image-out");
      props.target.current
        .classList.remove("image-in");
    }
  });
  return (
    <div className="feature-key-item">
      <div
        className={"feature-key-container " + (hoverState !== null ? 'isActive' : '')}
        onMouseEnter={() => sethoverState(featureData)}
        onMouseLeave={() => sethoverState(null)}
      >
        <div className="feature-title">{featureData.title}</div>
        <div className="feature-description">{featureData.description}</div>
      </div>
    </div>
  );
};

export default KeyFeatureItem;
