import React from "react";
import "./WhyMuvinSection.scss";
import { getImage } from "../../common-service";
import { LazyLoadImage } from "react-lazy-load-image-component";

const WhyMuvinSection = (props) => {
  const listData = props.listData;
  const isMobileState = props.isMobileState;
  return (
    <div className="why-muvin-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12 col-md-4">
            <div className="phone-wrapper">
              <LazyLoadImage  effect="opacity" src={isMobileState ? getImage('why-muvin-mobile.png') : getImage('why-muvin-image.png')} alt="Muvin" className="img-fluid" />
            </div>
          </div>
          <div className="col-12 col-md-8 col-lg-7 offset-lg-1">
            <div className="content-container">
              <div className="title-wrapper">
                <span className="text">
                  why <b>muvin</b>
                </span>
              </div>
              <div className="content-wrapper isList">
                <ul>
                  {listData.map((list, idx) => (
                  <li key={"whyMuvinList" + (idx + 1)} className="isListIcon mb-lg-20">
                    <div className="icon">
                      <img src={list.icon} alt="Muvin" />
                    </div>
                    <div className="text">
                      {list.point}
                    </div>
                  </li>
                  ))}
                </ul>
              </div>
              <div className="button-wrapper">
                <a href="/" className="btn btn-blue">
                  get started
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default WhyMuvinSection;
