import React from 'react'
import './KnowYourChildSection.scss'

const KnowYourChildSection = (props) => {
    return (
        <div className="know-your-child-wrapper">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <div className="content-container">
                  <div className="title-wrapper text-center wid-60 m-auto mb-30">
                    <span className="text">
                      <b>Take this quiz with your child to
                      understand their financial literacy</b>
                    </span>
                  </div>
                  <div className="button-wrapper d-flex justify-content-center">
                      <a href="/" className="btn btn-white">know your child</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    )
}

export default KnowYourChildSection
