import React, { useState, useEffect } from "react";
import "./CareersPage.scss";
import Banner from "../banner/Banner";
import Loader from "../common/loader/Loader";
import ChatWithUs from "../common/chat-with-us/ChatWithUs";
import CareersInfoDetails from "../careers-info-section/CareersInfoDetails";

import { getData, getStaticDataFunc, getWindowWidth, setStaticDataFunc } from '../../common-service';

const query = `{
    page(id: "2gUlFquxmzs5lbiFlupCgM") {
      articlesCollection(limit: 50) {
        items {
          ... on Section {
            sectionLabel
            titleMain
            titleSub
            description
            imagesMobileCollection(limit: 50) {
              items {
                url
              }
            }
            imagesCollection(limit: 50) {
              items {
                url
              }
            }
            cardsCollection(limit: 50) {
              items {
                ... on Career {
                  groupId
                  openingId
                  position
                  vertical
                  location
                  jobType
                  department
                  description
                }
              }
            }
          }
          ... on BusinessVerticals {
            name
            verticals
          }
        }
      }
    }
  }`;
const CareersPage = () => {
  const [page, setPage] = useState(null);
  let [isMobileState, setisMobileState] = useState(getWindowWidth);

  useEffect(() => {
    function handleResize() {
      setisMobileState(getWindowWidth);
    }

    setisMobileState(getWindowWidth);

    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  useEffect(() => {
    // Data call to get the dynamic content
    getData(query).then((res) => {
        if (res.data.data.page.assetsCollection) {
          setStaticDataFunc(res.data.data.page.assetsCollection.items)
        }
        setPage(res.data.data.page.articlesCollection.items);
      })
      .catch((err) => {
        console.error("Static content", err);
      });
  }, []);

  if (!page || getStaticDataFunc().length === 0) {
    return <Loader></Loader>;
  }
  return (
    <div className="w-100">
      <Banner
        data={page[0]}
        showGateway={false}
        isMobileState={isMobileState}
        bannerClass={"inner-page-banner"}
      ></Banner>
      <CareersInfoDetails data={page[2]} list={page[1]}></CareersInfoDetails>
      <ChatWithUs isMobileState={isMobileState}></ChatWithUs>
    </div>
  );
};

export default CareersPage;
