import React from "react";
import './Careers.scss';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { getImage } from "../../common-service";
import { Link } from "react-router-dom";

const Careers = (props) => {
  const posts = props.data.cardsCollection.items;
  return (
    <div className="career-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="content-container">
              <div className="title-wrapper isIcon inline mb-10">
                <span className="text">
                  <b>careers</b>
                </span>
                <span className="icon">
                  <LazyLoadImage effect="opacity" src={getImage('red-character.svg')} alt="Muvin" />
                </span>
              </div>
              <div className="content-wrapper wid-sm-100 wid-70 mb-30 mb-sm-20">
                want to work with us? here are some of the most recent job
                updates.
              </div>
              <div className="careers-posts-scroll">
                <div className="row">
                  {posts.map((post, idx) => (
                    <div key={'jobPost' + (idx + 1)} className="col-12 col-md-4">
                      <div className="careers-posts-item">
                        <div className="name">{post.position}</div>
                        <div className="location">
                          {post.vertical}, {post.location}
                        </div>
                        <div className="button-wrapper">
                          <a href="/" className="btn btn-link btn-blue">
                            read more
                          </a>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
              <div className="button-wrapper">
                <Link to="/careers" className="btn btn-blue">explore more</Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Careers;
