import React from "react";
import "./TeamSection.scss";
import { LazyLoadImage } from "react-lazy-load-image-component";
import ReactHtmlParser from "react-html-parser";
import { Link } from "react-router-dom";

const TeamSection = (props) => {
    const isMobileState = props.isMobileState;
  const teamContent = props.membersContent;
  const handlerTeamToggle = (events) => {
    let membersList = document.querySelectorAll(".team-item-wrapper");
    if (isMobileState) {
      debugger;
      const itemSrc = events.currentTarget.closest(".team-item-wrapper");
      const contentSrc = getChildHandler(itemSrc);
      if (itemSrc.classList.contains('isActive')) {
        itemSrc.classList.remove('isActive');
        itemSrc.style.maxHeight = '74px';
      } else {
        membersList.forEach((m) => {
          m.classList.remove("isActive")
          m.style.maxHeight = '74px';
        });
        itemSrc.classList.add('isActive');
        itemSrc.style.maxHeight = (contentSrc.offsetHeight) + 'px';
      }
    } else {
      if (
        events.currentTarget
          .closest(".team-item-wrapper")
          .classList.contains("isShow")
      ) {
        events.currentTarget
          .closest(".team-item-wrapper")
          .classList.remove("isShow");
          events.currentTarget
            .closest(".team-item-wrapper")
            .classList.add("isHide");
      } else {
        membersList.forEach((m) => m.classList.remove("isShow"));
        events.currentTarget
          .closest(".team-item-wrapper")
          .classList.add("isShow");
          events.currentTarget
            .closest(".team-item-wrapper")
            .classList.remove("isHide");
      }
    };
    }

    const getChildHandler = (parent) => {
      for (let i = 0; i < parent.childElementCount; i++) {
        if (parent.children[i].classList.contains('team-item-content')) {
          return parent.children[i];
        }
      }
    }
  return (
    <div className={"team-section-wrapper " + teamContent.theme}>
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="content-container">
              <div className="title-wrapper mb-sm-16 mb-30">
                <span className="text">
                  {ReactHtmlParser(teamContent.heading)}
                </span>
              </div>
            </div>
          </div>

          <div className="col-12">
            <div className="team-card-wrapper">
              <div className="row">
                {teamContent.content.map((team, idx) => (
                  <div
                    key={"teamMember" + (idx + 1)}
                    className="col-12 col-md-6 col-lg-4"
                  >
                    <div className={"team-item-wrapper " + ((isMobileState && idx === 0) ? 'isActive' : '')}>
                      <div className="thumbnail hidden-sm">
                        <img  effect="opacity"
                          src={team.thumbnail}
                          alt="Muvin"
                          className="img-fluid"
                        />
                      </div>
                      <div className="team-item-content">
                        <div className="title">
                          <div className="name">{team.name}</div>
                          <div className="designation">{team.designation}</div>
                          <div
                            className="action-button"
                            onClick={handlerTeamToggle}
                          >
                            <LazyLoadImage  effect="opacity" src={teamContent.triggerIcon} alt="Muvin" />
                          </div>
                        </div>
                      <div className="thumbnail hidden-md hidden-lg">
                        <img  effect="opacity"
                          src={team.thumbnail}
                          alt="Muvin"
                          className="img-fluid"
                        />
                      </div>
                        <div className="description">{team.description}</div>
                        <div className="social-media-icons">
                          {team.social.map((s, sIdx) => (
                          <Link to={s.link} className="icon">
                            <LazyLoadImage src={s.icon}/>
                          </Link>
                          ))}
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TeamSection;
