import React, { Fragment } from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import GatewaySection from "../gateway-section/GatewaySection";
import { getImage } from "../../common-service";

const Banner = (props) => {
  const isMobileState = props.isMobileState;
  const bannerClass = props.bannerClass;
  const buttoonWrapper = (
    <Fragment>
      <div className="button-wrapper isLink">
        <a href="/" className="btn btn-app btn-image btn-left-img">
          <LazyLoadImage effect="opacity" src={getImage('play-store-icon.svg')} alt="Muvin" />
          <span>download the app</span>
        </a>
        <a href="/" className="btn btn-app btn-image btn-left-img">
          <LazyLoadImage effect="opacity" src={getImage('app-store.svg')} alt="Muvin" />
          <span>download the app</span>
        </a>
      </div>
    </Fragment>
  );

  return (
    <div className={"w-100 " + (bannerClass !== undefined ? bannerClass : "")}>
      <div className="carousel-wrapper">
        <div className="carousel-body">
          <div className="carousel-body-left">
            <div className="text-wrapper">
              <b>{props.data.titleMain}</b>
              <span>{props.data.titleSub}</span>
            </div>
            {props.data.description !== null ? (
              <div className="description">{props.data.description}</div>
            ) : (
              ""
            )}
            {!isMobileState ? buttoonWrapper : ""}
          </div>
        </div>
        <div className="carousel-body-right">
          <LazyLoadImage
            effect="opacity"
            id="banner-image"
            src={
              isMobileState
                ? props.data.imagesMobileCollection.items[0].url
                : props.data.imagesCollection.items[0].url
            }
            alt="Muvin"
            className="img-fluid"
          />
        </div>
        {isMobileState ? buttoonWrapper : ""}
        {props.showGateway ? (
          <GatewaySection data={props.gatewayData}></GatewaySection>
        ) : (
          ""
        )}
      </div>
    </div>
  );
};

export default Banner;
