import React, { useEffect, useState } from "react";
import "./EducateYourChildItem.scss";

const EducateYourChildItem = (props) => {
  const clipDetails = props.clip;
  const [clipState, setclipState] = useState(null);

  useEffect((e) => {
    if (clipState) {
      props.videoTarget.current.children[0].src = clipState.url;
      props.videoTarget.current.classList.add("isShow");
      props.scrollTarget.current.children[props.index].classList.add(
        "isActive"
      );
      scrollItemToView(
        props.scrollTarget.current.children[props.index],
        props.scrollTarget.current.closest('.clip-body')
      );
    } else {
      props.videoTarget.current.classList.remove("isShow");
      props.scrollTarget.current.children[props.index].classList.remove(
        "isActive"
      );
    }
  });

  function scrollItemToView(element, container) {
    if (element.offsetTop < container.scrollTop) {
      container.scrollTop = element.offsetTop;
    } else {
      const offsetBottom = element.offsetTop + element.offsetHeight;
      const scrollBottom = container.scrollTop + container.offsetHeight;
      if (offsetBottom > scrollBottom) {
        container.scrollTop = offsetBottom - container.offsetHeight;
      }
    }
  }

  return (
    <div
      className="educate-your-child-item"
      onMouseEnter={(e) => setclipState(clipDetails)}
      data-target={props.index}
      onMouseLeave={(e) => setclipState(null)}
    >
      <div className="educate-your-child-item-container">
        <div className="heading">{clipDetails.title}</div>
        <div className="description">{clipDetails.description}</div>
      </div>
    </div>
  );
};

export default EducateYourChildItem;
