import React from "react";
import './ChildrenSection.scss';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { Link } from "react-router-dom";
import { getImage } from '../../common-service';

const ChildrenSection = (props) => {
  const isMobileState = props.isMobileState
  return (
    <div className="children-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12 col-md-5">
            <div className="phone-wrapper">
              <LazyLoadImage effect="opacity"  src={isMobileState ? getImage("app-made-for-children-mobile.png") : getImage("app-made-for-childrens.png")} alt="Muvin" />
            </div>
          </div>
          <div className="col-12 col-md-7">
            <div className="content-container">
              <div className="title-wrapper isIcon right">
                <span className="text">
                  app made for <b>children</b>
                </span>
                <span className="icon hidden-sm">
                  <LazyLoadImage effect="opacity"src={getImage('purple2-character.png')} alt="Muvin" />
                </span>
              </div>
              <div className="content-wrapper">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
                enim ad minim veniam, quis nostrud exercitation ullamco laboris
                nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor
                in reprehenderit in voluptate velit esse cillum dolore eu fugiat
                nulla pariatur.
              </div>
              <div className="button-wrapper">
                <Link to="/children" className="btn btn-blue">know more</Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChildrenSection;
