import React from "react";
import "./SecuritySection.scss";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { getImage } from "../../common-service";

const SecuritySection = (props) => {
  const isMobileState = props.isMobileState; 
  return (
    <div className="secure-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12 col-md-6">
            <div className="phone-wrapper">
              <LazyLoadImage effect="opacity" src={isMobileState ? getImage('secure-mobile.png') : getImage('secure.svg')} alt="Muvin" />
            </div>
          </div>
          <div className="col-12 col-md-6">
            <div className="content-container">
              <div className="title-wrapper isIcon top">
                <span className="icon hidden-sm">
                  <LazyLoadImage effect="opacity" src={getImage('green-character.png')} alt="Muvin" className="wid-34" />
                </span>
                <span className="text">
                  your details are <b>secure</b>
                </span>
              </div>
              <div className="content-wrapper">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
                enim ad minim veniam, quis nostrud exercitation ullamco laboris
                nisi ut aliquip ex ea commod.
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SecuritySection;
