import React from "react";
import './Metrics.scss';
import { LazyLoadImage } from 'react-lazy-load-image-component';

const Metrics = (props) => {
  let a = 0;
  const handleScroll = () => {
    if (document.querySelector("#metric-wrapper")) {
      var oTop =
        document.querySelector("#metric-wrapper").offsetTop - window.innerHeight;
      if (a === 0 && document.querySelector("#metric-wrapper").scrollTop > oTop) {
        let counterRef = document.querySelectorAll(".count");
        counterRef.forEach((item) => {
          counter(
            item.getAttribute("id"),
            0,
            parseInt(item.getAttribute("data-counter")),
            10
          );
        });
        a = 1;
      }
    }
  };

  window.addEventListener("scroll", handleScroll);

  function counter(id, start, end, duration) {
    let obj = document.getElementById(id),
      initialCount = obj.textContent,
      current = start,
      range = end - start,
      increment =
        end > start
          ? (range.toString().length * 100) / range.toString().length
          : -1;
    let timer = setInterval(() => {
      current += increment;
      obj.textContent = current;
      setTimeout(function () {
        clearInterval(timer);
        obj.textContent = initialCount;
      }, 1000);
    }, duration);
  }
  const metricsData = props.data.cardsCollection.items;
  return (
    <div className="metric-wrapper" id="metric-wrapper">
      <div className="container">
        <div className="row">
          {metricsData.map((metric, idx) => (
            <div key={"metrics" + (idx + 1)} className="col-12 col-md-4">
              <div className="metric-item">
                <div className="icon">
                  <LazyLoadImage effect="opacity" src={metric.metricIcon.url} alt="Muvin" />
                </div>
                <div className="counter">
                  <span
                    id={"metric-counter" + (idx + 1)}
                    className="count"
                    data-counter={metric.metricValue.replace(/[^\d]/g, "")}
                  >
                    {metric.metricValue}
                  </span>
                </div>
                <div className="title">{metric.metricLabel}</div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Metrics;
