import React, { useState, useEffect, createRef } from "react";
import "./BlogDescription.scss";
import { useParams } from "react-router";
import { Link, NavLink } from "react-router-dom";
import ChatWithUs from "../../common/chat-with-us/ChatWithUs";
import Loader from "../../common/loader/Loader";
import "./BlogDescription.scss";
import { documentToReactComponents } from "@contentful/rich-text-react-renderer";
import { useLocation } from "react-router-dom";

import shareIcon from "../../../assets/images/share-icon.png";
import facebookIcon from "../../../assets/images/facebook-icon.png";
import {
  getData,
  getWindowWidth,
  setStaticDataFunc,
} from "../../../common-service";

const BlogDescription = (props) => {
  var { slug } = useParams();
  let [isMobileState, setisMobileState] = useState(getWindowWidth);
  const [page, setPage] = useState(null);

  const query = `{
      blogPostModel(id: "${slug}") {
            blogThumbnail {
              url
            }
            publicationDate
            blogPostTitle
            blogPostAuthor
            blogTags
            relatedBlog
            description {
                json
            }
        }
    }`;
  let data = useLocation();
  let relatedBlogs = data?.userProps?.blogPosts;
  const blogPosts = relatedBlogs;
  const filteredRelatedBlog = [];
  if (data?.userProps?.blogPosts.length !== 0) {
    const seperateBlogs = relatedBlogs.filter((a) => a.slug !== slug);
    const selectedBlogIndex = relatedBlogs.filter((a) => a.slug === slug)[0];
    const selectedBlogTagList = selectedBlogIndex.blogTags;
    selectedBlogTagList.forEach((b) => {
      seperateBlogs.forEach((c) => {
        c.blogTags.forEach((e) => {
          if (b === e) {
            if (
              filteredRelatedBlog.filter((f) => f.slug === c.slug).length === 0
            ) {
              filteredRelatedBlog.push(c);
            }
          }
        });
      });
    });
  }
  const blogTitleArr = Array.from({ length: filteredRelatedBlog.length }, (a) =>
    createRef(null)
  );
  const blogAuthorArr = Array.from(
    { length: filteredRelatedBlog.length },
    (a) => createRef(null)
  );

  const scrollTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  useEffect(() => {
    // Resize method
    function handleResize() {
      setisMobileState(getWindowWidth);
    }

    setisMobileState(getWindowWidth);

    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, [query]);

  useEffect(() => {
    blogTitleArr.forEach((b, i) => {
      if (b.current !== null) {
        b.current.style.marginBottom = 108 - b.current.offsetHeight + 10 + "px";
      }
    });
    const slider = document.querySelector(".tags-container");
    let isDown = false;
    let startX;
    let scrollLeft;
    const mouseDownHandler = (e) => {
      isDown = true;
      slider.classList.add("active");
      startX = e.pageX - slider.offsetLeft;
      scrollLeft = slider.scrollLeft;
    };
    const mouseLeaveHandler = (e) => {
      isDown = false;
      slider.classList.remove("active");
    };
    const mouseUpHandler = (e) => {
      isDown = false;
      slider.classList.remove("active");
    };
    const mouseMoveHandler = (e) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - slider.offsetLeft;
      const walk = x - startX;
      slider.scrollLeft = scrollLeft - walk;
    };
    if (slider) {
      slider.addEventListener("mousedown", mouseDownHandler);
      slider.addEventListener("mouseleave", mouseLeaveHandler);
      slider.addEventListener("mouseup", mouseUpHandler);
      slider.addEventListener("mousemove", mouseMoveHandler);
      slider.addEventListener("mousemove", mouseMoveHandler);
    }
    return () => {
      if (slider) {
        slider.removeEventListener("mousedown", mouseDownHandler);
        slider.removeEventListener("mouseleave", mouseLeaveHandler);
        slider.removeEventListener("mouseup", mouseUpHandler);
        slider.removeEventListener("mousemove", mouseMoveHandler);
        slider.removeEventListener("mousemove", mouseMoveHandler);
      }
    };
  }, [blogTitleArr, blogAuthorArr]);

  useEffect(() => {
    // Data call to get the dynamic content
    getData(query)
      .then((res) => {
        if (res.data.data?.assetsCollection) {
          setStaticDataFunc(res.data.data.page.assetsCollection.items);
        }
        setPage(res.data.data.blogPostModel);
      })
      .catch((err) => {
        console.error("Static content", err);
      });
  }, [query]);

  if (!page) {
    return <Loader></Loader>;
  }

  return (
    <div className="blog-description-wrapper">
      <div className="banner-wrapper"></div>
      <div className="container">
        <div className="tittle-wrapper">
          <Link to="/blog" className="back-button"></Link>
          <div className="title">{page.blogPostTitle}</div>
          <div className="blog-details">
            <div className="author-name">by {page.blogPostAuthor}</div>
            <div className="publish-date">
              {new Date(page.publicationDate).getDate()}{" "}
              {new Date(page.publicationDate).toLocaleString("default", {
                month: "long",
              })}{" "}
              {new Date(page.publicationDate).getFullYear()}
            </div>
          </div>
        </div>
        <div
          className="blog-thumbnail"
          style={{ backgroundImage: `url(${page.blogThumbnail.url})` }}
        ></div>
        <div className="blog-content">
          {documentToReactComponents(page.description.json)}
        </div>
        <div className="related-blogs-wrapper hidden">
          <div className="related-blogs-title">Related Blogs</div>
          <div className="related-blogs-container">
            <div className="blog-wrapper">
              <div className="container">
                <div className="row blog-wrapper-row">
                  <div className="row blog-wrapper-row">
                    {filteredRelatedBlog.map((post, idx) =>
                      idx < 3 ? (
                        <div
                          key={"blogPost" + (idx + 1)}
                          className="col-12 col-md-4 blog-wrapper-col"
                        >
                          <div className="blog-wrapper-wrapper">
                            <div
                              className="thumbnail"
                              style={{
                                backgroundImage: `url(${post.blogThumbnail.url})`,
                              }}
                            ></div>
                            <div className="content-wrapper">
                              <div className="publication-date">
                                {new Date(post.publicationDate).getDate()}{" "}
                                {new Date(post.publicationDate).toLocaleString(
                                  "default",
                                  {
                                    month: "long",
                                  }
                                )}{" "}
                                {new Date(post.publicationDate).getFullYear()}
                              </div>
                              <div className="title" ref={blogTitleArr[idx]}>
                                {post.blogPostTitle}
                              </div>
                              <div
                                className="author-name hidden"
                                ref={blogAuthorArr[idx]}
                              >
                                by {post.blogPostAuthor}
                              </div>
                              <div className="blog-tags hidden">
                                <div className="tags-title">tags:</div>
                                <div className="tags-container">
                                  {post.blogTags.map((tag, i) => (
                                    <span
                                      className="tags"
                                      key={"blogTags" + (i + 1)}
                                    >
                                      {tag}
                                    </span>
                                  ))}
                                </div>
                              </div>
                              <div className="post-footer">
                                <div className="social-media hidden">
                                  <Link to="/">
                                    <img src={shareIcon} alt="Muvin" />
                                  </Link>
                                  <Link to="/">
                                    <img src={facebookIcon} alt="Muvin" />
                                  </Link>
                                </div>
                                <div className="button-wrapper w-100">
                                  <NavLink
                                    to={{
                                      pathname: "/blog/" + post.slug,
                                      userProps: { blogPosts },
                                    }}
                                    onClick={scrollTop}
                                    className="btn btn-link btn-blue"
                                  >
                                    read more
                                  </NavLink>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      ) : (
                        ""
                      )
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <ChatWithUs isMobileState={isMobileState}></ChatWithUs>
    </div>
  );
};

export default BlogDescription;
