import React, { useState, useEffect } from "react";
import Banner from "../banner/Banner";
import { LazyLoadImage } from "react-lazy-load-image-component";
import ChildrensKeyFeatures from "../childrens-key-features/ChildrensKeyFeatures";
import ChatWithUs from "../common/chat-with-us/ChatWithUs";
import Loader from "../common/loader/Loader";
import SocialSection from "../common/social-section/SocialSection";
import FaqSection from "../faq-section/FaqSection";
import { ParentsCardSection } from "../parents-card-section/ParentsCardSection";
import PartnersSection from "../partners-section/PartnersSection";
import WhyMuvinSection from "../why-muvin-section/WhyMuvinSection";
import "./ChildrenComponent.scss";

import {
  getData,
  getImage,
  getStaticDataFunc,
  getWindowWidth,
  setStaticDataFunc,
} from "../../common-service";

const query = `{
  page(id: "2X7buRaegarolL9Dn6IjHL") {
    assetsCollection {
      items {
        fileName
        url
      }
    }
    articlesCollection(limit: 50) {
      items {
        ... on Section {
          sectionLabel
          titleMain
          titleSub
          description
          imagesMobileCollection (limit: 50) {
              items {
                  url
              }
          }
          imagesCollection (limit: 50) {
              items {
                  url
              }
          }
          cardsCollection(limit: 50) {
            items {
              ... on Faq {
                query
                description
              }
            }
          }
        }
      }
    }
  }
}`;

const ChildrenComponent = () => {
  const [page, setPage] = useState(null);

  useEffect(() => {
    getData(query).then((res) => {
        setStaticDataFunc(res.data.data.page.assetsCollection.items);
        setPage(res.data.data.page.articlesCollection.items);
      })
      .catch((err) => {
        console.error("Static content", err);
      });
  }, []);

  let [isMobileState, setisMobileState] = useState(getWindowWidth);

  useEffect(() => {
    function handleResize() {
      setisMobileState(getWindowWidth);
    }

    setisMobileState(getWindowWidth);

    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const contentBody = {
    heading: "never pay<br /> <b>full price again</b>",
    description:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.  Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris.",
    linkWrapper: {
      isLink: false,
      linkText: null,
      linkHref: null,
    },
  };
  const whyMuvinListChildren = [
    {
      icon: getImage("book-icon.svg"),
      point:
        "muvin lets you set your financial goals and keep a track on your expenses and helps inculcate the habit of saving at an early age",
    },
    {
      icon: getImage("notepad-icon.svg"),
      point:
        "Learn how money works with our engaging money management tool and easy to understand educational content",
    },
    {
      icon: getImage("eye-icon.svg"),
      point:
        "Go cashless with muvin Card for purchases both online or in-store",
    },
  ];

  if (!page || getStaticDataFunc().length === 0) {
    return <Loader></Loader>;
  }

  return (
    <div className="w-100">
      <Banner
        data={page[0]}
        showGateway={false}
        isMobileState={isMobileState}
      ></Banner>
      <WhyMuvinSection
        isMobileState={isMobileState}
        listData={whyMuvinListChildren}
      ></WhyMuvinSection>
      {/* Account Setup */}
      <div className="easy-account-wrapper">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="content-container">
                <div
                  className={
                    "title-wrapper mb-50 " +
                    (!isMobileState ? "text-center" : "")
                  }
                >
                  <span className="text">
                    easy {isMobileState ? <br /> : ""} account <b>setup</b>
                  </span>
                </div>
              </div>
            </div>
            <div className="col-12 easy-account-steps-wrapper mb-30">
              <div className="row">
                <div className="col-12 col-md-4 easy-account-steps-col">
                  <div className="easy-account-steps-item">
                    <div className="image">
                      <LazyLoadImage
                        effect="opacity"
                        src={getImage("donwload-acc-icon.svg")}
                        alt="Muvin"
                      />
                    </div>
                    <div className="content-body">
                      <div className="title">
                        <span>download the app</span>
                      </div>
                      <div className="description">
                        Download the app and create your own account in seconds
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-12 col-md-4 easy-account-steps-col">
                  <div className="easy-account-steps-item">
                    <div className="image">
                      <LazyLoadImage
                        effect="opacity"
                        src={getImage("invite-your-child.svg")}
                        alt="Muvin"
                      />
                    </div>
                    <div className="content-body">
                      <div className="title">
                        <span>invite your parent</span>
                      </div>
                      <div className="description">
                        Invite your parent to begin your financial
                        transformation
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-12 col-md-4 easy-account-steps-col">
                  <div className="easy-account-steps-item">
                    <div className="image">
                      <LazyLoadImage
                        effect="opacity"
                        src={getImage("submit-kyc.svg")}
                        alt="Muvin"
                      />
                    </div>
                    <div className="content-body">
                      <div className="title">
                        <span>Get on a financial joy ride with muvin</span>
                      </div>
                      <div className="description">
                        Start using the app to earn, save and spend and get
                        rewarded
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="button-wrapper d-flex justify-content-center">
                <a href="/" className="btn btn-blue">
                  get started
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* Account Setup */}
      <ChildrensKeyFeatures
        isMobileState={isMobileState}
      ></ChildrensKeyFeatures>
      <PartnersSection
        data={page[1]}
        contentBody={contentBody}
        isMobileState={isMobileState}
      ></PartnersSection>
      <ParentsCardSection isMobileState={isMobileState}></ParentsCardSection>
      <SocialSection
        data={page[2]}
        isMobileState={isMobileState}
      ></SocialSection>
      <FaqSection data={page[3]} isMobileState={isMobileState}></FaqSection>
      <ChatWithUs isMobileState={isMobileState}></ChatWithUs>
    </div>
  );
};

export default ChildrenComponent;
