import React, { Fragment } from "react";
import "./ChildrensKeyFeatures.scss";
import { LazyLoadImage } from "react-lazy-load-image-component";
import ReactHtmlParser from "react-html-parser";
import { getImage } from "../../common-service";

const ChildrensKeyFeatures = (props) => {
  const isMobileState = props.isMobileState;
  const featuresList = [
    {
      icon: getImage('childrens-add-receive-icon.svg'),
      title: "send and receive money",
      description:
        "You can request money from your parents and also transfer money to your friends",
      imageUrl: getImage('send-and-receive-money.png'),
    },
    {
      icon: getImage('childrens-set-goals-icon.svg'),
      title: "track all expenses",
      description: "Categorise your spending and analyse them to understand where your money is going. Get transaction alerts in real time.",
      imageUrl: getImage('set-goals.png'),
    },
    {
      icon: getImage('childrens-track-spending-icon.svg'),
      title: "contactless payment using muvin Card",
      description: "Make hassle free payments in less than 5 seconds which work without a phone",
      imageUrl: getImage('track-spending.png'),
    },
  ];

  const titleFragment = (
    <Fragment>
      <div className="row children-key-features-heading">
        <div className="col-12">
          <div className="content-container">
            <div
              className={
                "title-wrapper isIcon inline mb-50 " +
                (!isMobileState ? "justify-content-center" : "")
              }
            >
              <span className="text text-center">
                key <b>features</b>
              </span>
              <span className="icon">
                <LazyLoadImage  effect="opacity" src={getImage('green-character.svg')} alt="Muvin" />
              </span>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );

  return (
    <div className="children-key-features-wrapper">
      <div className="container">
        {!isMobileState ? titleFragment : ""}
        <div className="children-key-features-container">
          {featuresList.map((feature, idx) => (
            <div
              key={"childrenKeyFeature" + (idx + 1)}
              className="row children-key-features-row"
            >
              <div
                className={
                  "col-12 col-sm-6 children-key-features-col " +
                  ((idx + 1) % 2 === 0 && !isMobileState ? "order-2" : "")
                }
              >
                <div className="phone-wrapper">
                  <LazyLoadImage  effect="opacity" src={feature.imageUrl} alt="Muvin" />
                </div>
              </div>
              {((idx + 1) === 1) && isMobileState ? (
                titleFragment
              ) : (isMobileState ? (
                <div className="placeholder-label"></div>
                ) : ("")
              )}
              <div
                className={
                  "col-12 col-sm-6 children-key-features-col " +
                  ((idx + 1) % 2 === 0 ? "order-1" : "")
                }
              >
                <div className="children-key-feature-container">
                  <div className="heading">
                    <div className="icon">
                      <LazyLoadImage  effect="opacity" src={feature.icon} alt="Muvin" />
                    </div>
                    <div className="title">{feature.title}</div>
                  </div>
                  <div className="description">
                    {ReactHtmlParser(feature.description)}
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ChildrensKeyFeatures;
