import React from "react";
import "./Footer.scss";
import { LazyLoadImage } from "react-lazy-load-image-component";
import stickyLogo from "../../../assets/images/footer-logo.svg";
import playStoreLogo from "../../../assets/images/play-store-icon.png";
import appStoreLogo from "../../../assets/images/app-store.png";
import locationMarker from "../../../assets/images/location-marker.svg";
import mailMarker from "../../../assets/images/mail-icon.svg";
import { Link } from "react-router-dom";

const Footer = (props) => {
  const isMobileState = props.isMobileState;
  return (
    <div className="footer-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12 col-md-4 order-0">
            <div className="footer-info-wrapper">
              <div className="logo mb-40">
                <LazyLoadImage  effect="opacity"
                  src={stickyLogo}
                  alt="Muvin"
                  className="img-fluid wid-64"
                />
              </div>
              <div className="button-wrapper isLink mb-30">
                <a
                  href="/"
                  className={
                    "btn btn-app btn-blue btn-image btn-left-img " +
                    (isMobileState ? "btn-black-text" : "")
                  }
                >
                  <LazyLoadImage  effect="opacity" src={playStoreLogo} alt="Muvin" />
                  <span>download the app</span>
                </a>
                <a
                  href="/"
                  className={
                    "btn btn-app btn-blue btn-image btn-left-img " +
                    (isMobileState ? "btn-black-text" : "")
                  }
                >
                  <LazyLoadImage  effect="opacity" src={appStoreLogo} alt="Muvin" />
                  <span>download the app</span>
                </a>
              </div>
            </div>
          </div>
          <div
            className={"col-12 col-md-4 " + (isMobileState ? "order-3" : "")}
          >
            <div
              className={
                "address-wrapper " + (!isMobileState ? "isSeperator" : "")
              }
            >
              <ul>
                <li className="address">
                  <span className="icon">
                    <LazyLoadImage  effect="opacity" src={locationMarker} alt="Muvin" />
                  </span>
                  <div className="text">
                    wework salarpuria magnificia, <br />
                    bengaluru 560016
                  </div>
                </li>
                <li className="mail">
                  <span className="icon">
                    <LazyLoadImage  effect="opacity" src={mailMarker} alt="Muvin" />
                  </span>
                  <a href="mailto:contact@muvin.in">contact@muvin.in </a>
                </li>
              </ul>
            </div>
          </div>
          <div
            className={"col-12 col-md-4 " + (isMobileState ? "order-2" : "")}
          >
            <div className="quicklinks-wrapper">
              <ul>
                <li>
                  <Link to="/parents">parents</Link>
                </li>
                <li>
                  <Link to="/childrens">children</Link>
                </li>
                <li>
                  <Link to="/about">about</Link>
                </li>
                <li>
                  <Link to="/partners">partners</Link>
                </li>
                <li>
                  <Link to="/careers">careers</Link>
                </li>
                <li>
                  <Link to="/blog">blogs</Link>
                </li>
              </ul>
            </div>
          </div>
          <div
            className={"col-12 col-md-4 " + (isMobileState ? "order-4" : "")}
          >
            <div className="quicklinks-wrapper privacy-wrapper">
              <ul>
                <li>
                  <Link to="/pripacy-policy">privacy policy</Link>
                </li>
                <li>
                  <Link to="/terms-and-conditions">terms and conditions</Link>
                </li>
              </ul>
              <div className="copyrights-wrapper">
                &#169; {new Date().getFullYear()} finone technologies pvt. ltd.
                all rights reserved
              </div>
            </div>
          </div>
          <div
            className={"col-12 col-md-4 " + (isMobileState ? "order-3" : "")}
          >
            <div
              className={
                "address-wrapper inDesktop " + (!isMobileState ? "isSeperator" : "")
              }
            ></div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Footer;
