import React, { useState } from "react";
import "./Testimonials.scss";
import Carousel from "react-bootstrap/Carousel";
import { LazyLoadImage } from 'react-lazy-load-image-component';

const Testimonials = (props) => {
  const [index, setIndex] = useState(0);
  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
  };
  const testimonials = props.data.cardsCollection.items;
  
  return (
    <div className="testimonial-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="testimonial-heading">testimonials</div>
            <div className="testimonial-carousel">
              <Carousel activeIndex={index} onSelect={handleSelect}>
                {testimonials.map((slide, i) => {
                  return (
                    <Carousel.Item key={"testimonail" + (i + 1)}>
                      <div className="testimonial-carousel-item">
                        <div className="image">
                          <LazyLoadImage effect="opacity"
                            src={slide.customerImage.url}
                            alt="Muvin"
                            className="img-fluid"
                          />
                        </div>
                        <div className="content-wrapper">
                          <div className="description">{slide.description}</div>
                          <div className="name">{slide.name}</div>
                        </div>
                      </div>
                    </Carousel.Item>
                  );
                })}
              </Carousel>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Testimonials;
