import axios from "axios";

let staticData = [];

export const setStaticDataFunc = (data) => {
    if (staticData.length !== 0) {
        data.forEach(d => {
            if (staticData.filter(f => f.fileName === d.fileName).length === 0) {
                staticData.push(d);
            }
        })
    } else {
        data.forEach(d => {
            staticData.push(d);
        })
    }
}

export const getStaticDataFunc = () => {
    return staticData;
}

export const getImage = (name) => {
    if (staticData.length !== 0) {
        if (staticData.filter(i => i.fileName === name).length !== 0) {
            const imageUrl = staticData.filter(i => i.fileName === name)[0]
            return imageUrl.url;
        }
    }
}
export const getWindowWidth = () => {
    let state = false;
    if (window.innerWidth < 767) {
        state = true;
    } else {
        state = false;
    }
    return state;
}

export const getData = async (query) => {
    let data = await axios.post(process.env.REACT_APP_BASE_URL, JSON.stringify({
        query: query
    }), {
        headers: {
            "Content-Type": "application/json",
            Authorization: "Bearer " + process.env.REACT_APP_ACCESS_TOKEN,
        },
    })

    return data;
}