import React from "react";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { getImage } from "../../common-service";


const OurStorySection = (props) => {
  const isMobileState = props.isMobileState;

  return (
    <div className="our-story-wrapper">
      <div className="container">
        <div className="row">
          <div className={"col-12 col-md-6 " + (isMobileState ? "order-2" : "order-1")}>
            <div className="content-container">
              <div className="title-wrapper mb-0 isIcon right">
                <span className="text">
                  our <b>story</b>
                </span>
              </div>
              <div className="content-wrapper">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
                enim ad minim veniam, quis nostrud exercitation ullamco laboris
                nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor
                in reprehenderit in voluptate velit esse cillum dolore eu fugiat
                nulla pariatur.
              </div>
              <div className="button-wrapper">
                <a href="/" className="btn btn-blue">
                  know more about us
                </a>
              </div>
            </div>
          </div>
          <div className={"col-12 col-md-6 " + (isMobileState ? "order-1" : "order-2")}>
            <div className="phone-wrapper our-story-image">
              <LazyLoadImage effect="opacity" src={getImage('hey-there.png')} alt="Muvin" className="wid-80" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OurStorySection;
