import React from "react";
import "./YourOwnCardSection.scss";
import { getImage } from "../../common-service";

const YourOwnCardSection = (props) => {
  const isMobileState = props.isMobileState;
  return (
    <div className="personel-card">
      <div className="container">
        <div className="row">
          <div className="col-12 col-md-5">
            <div className="phone-wrapper">
              <img
                src={isMobileState ? getImage('your-childs-personal-card-mobile.png') : getImage('your-childs-personal-card.png')}
                alt="Muvin"
                className="img-fluid"
              />
            </div>
          </div>
          <div className="col-12 col-md-7 col-lg-6 offset-lg-1">
            <div className="content-container">
              <div className="title-wrapper isIcon inline right">
                <span className="text">
                  <span className="right-inline">
                    your child’s
                    <img src={getImage('purple2-character.png')} alt="Muvin" /> <br />
                  </span>
                  <b>personal card</b>
                </span>
              </div>
              <div className="content-wrapper isList isBullets">
                <ul>
                  <li>
                    Go cashless with muvinCard card. It can be used for
                    purchases in digital and physical world
                  </li>
                  <li>Contactless card for purchases</li>
                  <li>
                    Transactions protected with second factor authentication
                    (PIN or OTP)
                  </li>
                  <li>Block / Unblock the card with just a click</li>
                  <li>Limit your daily spending</li>
                  <li>Alerts yourself by defining category level limits</li>
                  <li>Choose your favourite card design</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default YourOwnCardSection;
