import React from "react";
import "./ArticlesSection.scss";
import { LazyLoadImage } from 'react-lazy-load-image-component';

const ArticlesSection = (props) => {
  const articles = props.data.imagesCollection.items;
  return (
    <div className="articles-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="articles-title text-center">
              mentioned as India’s largest youth neobank
            </div>
            <div className="articles-list container-fluid">
              <ul className="row">
                {articles.map((article, idx) => (
                  <li key={'article' + (idx + 1)} className="col-12 col-sm-6 col-md-3">
                    <LazyLoadImage effect="opacity" src={article.url} alt="Muvin" className="img-fluid" />
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ArticlesSection;
