import React, { useState, useEffect } from "react";
import "./BlogPage.scss";
import Banner from "../../banner/Banner";
import Loader from "../../common/loader/Loader";
import ChatWithUs from "../../common/chat-with-us/ChatWithUs";
import BlogList from "../blogs-list/BlogList";
import {
  getData,
  getWindowWidth,
  setStaticDataFunc,
} from "../../../common-service";

const query = `{
    page(id: "2zSz3m9zt2sQ6FrCZ7JVAz") {
      articlesCollection(limit: 50) {
        items {
          ... on Section {
            sectionLabel
            titleMain
            titleSub
            description
            imagesMobileCollection(limit: 50) {
              items {
                url
              }
            }
            imagesCollection(limit: 50) {
              items {
                url
              }
            }
            cardsCollection(limit: 50) {
              items {
                ... on BlogPostModel {
                  slug
                  blogThumbnail {
                    url
                  }
                  publicationDate
                  blogPostTitle
                  blogPostAuthor
                  blogTags
                  relatedBlog
                }
              }
            }
          }
        }
      }
    }
  }
  `;

const BlogPage = () => {
  const [page, setPage] = useState(null);

  let [isMobileState, setisMobileState] = useState(getWindowWidth);

  useEffect(() => {
    // Resize method
    function handleResize() {
      setisMobileState(getWindowWidth);
    }

    setisMobileState(getWindowWidth);

    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  useEffect(() => {
    // Data call to get the dynamic content
    getData(query)
      .then((res) => {
        if (res.data.data.page.assetsCollection) {
          setStaticDataFunc(res.data.data.page.assetsCollection.items);
        }
        setPage(res.data.data.page.articlesCollection.items);
      })
      .catch((err) => {
        console.error("Static content", err);
      });
  }, []);

  if (!page) {
    return <Loader></Loader>;
  }
  return (
    <div className="w-100">
      <Banner
        data={page[0]}
        showGateway={false}
        isMobileState={isMobileState}
        bannerClass={"blogs-page-banner"}
      ></Banner>
      <BlogList data={page[1]} isMobileState={isMobileState}></BlogList>
      <ChatWithUs isMobileState={isMobileState}></ChatWithUs>
    </div>
  );
};

export default BlogPage;
