import React, { useState, useEffect } from "react";
import "./ParentsComponent.scss";
import { LazyLoadImage } from "react-lazy-load-image-component";
import Banner from "../banner/Banner";
import Loader from "../common/loader/Loader";
import ChatWithUs from "../common/chat-with-us/ChatWithUs";
import FaqSection from "../faq-section/FaqSection";
import SocialSection from "../common/social-section/SocialSection";
import PricingSection from "../pricing-section/PricingSection";
import Testimonials from "../testimonials/Testimonials";
import PartnersSection from "../partners-section/PartnersSection";
import WhyMuvinSection from "../why-muvin-section/WhyMuvinSection";
import KeyFeatures from "../key-features/KeyFeatures";
import EducateYourChild from "../educate-your-child/EducateYourChild";
import KnowYourChildSection from "../know-your-child-section/KnowYourChildSection";
import YourOwnCardSection from "../your-own-card-section/YourOwnCardSection";

import {
  getData,
  getImage,
  getStaticDataFunc,
  getWindowWidth,
  setStaticDataFunc,
} from "../../common-service";

const query = `{
  page(id: "7E43H981M6BYg8wqNbSXIH") {
    assetsCollection {
      items {
        fileName
        url
      }
    }
    articlesCollection(limit: 50) {
      items {
        ... on Section {
          sectionLabel
          titleMain
          titleSub
          description
          imagesMobileCollection (limit: 50) {
              items {
                  url
              }
          }
          imagesCollection (limit: 50) {
              items {
                  url
              }
          }
          cardsCollection(limit: 50) {
            items {
              ... on Faq {
                query
                description
              }
              ... on Testimonial {
                name
                description
                customerImage {
                  url
                }
              }
            }
          }
        }
      }
    }
  }
}
`;

const ParentsComponent = () => {
  const [page, setPage] = useState(null);

  let [isMobileState, setisMobileState] = useState(getWindowWidth);

  useEffect(() => {
    // API call to get the dynamic content for parents page
    
      getData(query).then((res) => {
        setStaticDataFunc(res.data.data.page.assetsCollection.items);
        setPage(res.data.data.page.articlesCollection.items);
      })
      .catch((err) => {
        console.error("Static content", err);
      });
  }, []);

  useEffect(() => {
    function handleResize() {
      setisMobileState(getWindowWidth);
    }

    setisMobileState(getWindowWidth);

    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const contentBody = {
    heading: "our <b>partners</b> <br /> and <b>offers</b>",
    description: null,
    linkWrapper: {
      isLink: true,
      linkText: "get started",
      linkHref: "/",
    },
  };

  const whyMuvinListParents = [
    {
      icon: getImage("book-icon.svg"),
      point:
        "muvin provides a platform allowing children to manage their own money in the real world, making decisions to spend, save and earn while parents get complete visibility",
    },
    {
      icon: getImage("notepad-icon.svg"),
      point:
        "Helps your children to become financially savvy and encourage good money habits by providing them opportunity to earn through rewards well deserved by them",
    },
    {
      icon: getImage("eye-icon.svg"),
      point:
        "Promotes financial literacy and independence, by providing children the opportunity to practice and improve how they manage money, and keep within budget",
    },
  ];
  if (!page || getStaticDataFunc().length === 0) {
    return <Loader></Loader>;
  } 

  return (
    <div className="w-100">
      <Banner
        data={page[0]}
        showGateway={false}
        isMobileState={isMobileState}
      ></Banner>
      <WhyMuvinSection
        isMobileState={isMobileState}
        listData={whyMuvinListParents}
      ></WhyMuvinSection>
      <KeyFeatures isMobileState={isMobileState}></KeyFeatures>
      <EducateYourChild isMobileState={isMobileState}></EducateYourChild>
      <KnowYourChildSection
        isMobileState={isMobileState}
      ></KnowYourChildSection>
      {/* Account Setup */}
      <div className="easy-account-wrapper">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="content-container">
                <div
                  className={
                    "title-wrapper mb-50 " +
                    (!isMobileState ? "text-center" : "")
                  }
                >
                  <span className="text">
                    easy {isMobileState ? <br /> : ""} account <b>setup</b>
                  </span>
                </div>
              </div>
            </div>
            <div className="col-12 easy-account-steps-wrapper mb-30">
              <div className="row">
                <div className="col-12 col-md-4 easy-account-steps-col">
                  <div className="easy-account-steps-item">
                    <div className="image">
                      <LazyLoadImage  effect="opacity"
                        src={getImage("donwload-acc-icon.svg")}
                        alt="Muvin"
                      />
                    </div>
                    <div className="content-body">
                      <div className="title">
                        <span>download the app</span>
                      </div>
                      <div className="description">
                        Download the app and create your own parent account in
                        seconds. Quickly complete the KYC
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-12 col-md-4 easy-account-steps-col">
                  <div className="easy-account-steps-item">
                    <div className="image">
                      <LazyLoadImage  effect="opacity"
                        src={getImage("invite-your-child.svg")}
                        alt="Muvin"
                      />
                    </div>
                    <div className="content-body">
                      <div className="title">
                        <span>invite your child</span>
                      </div>
                      <div className="description">
                        Invite your kids to kickstart their journey to being
                        financially independent
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-12 col-md-4 easy-account-steps-col">
                  <div className="easy-account-steps-item">
                    <div className="image">
                      <LazyLoadImage  effect="opacity" src={getImage("submit-kyc.svg")} alt="Muvin" />
                    </div>
                    <div className="content-body">
                      <div className="title">
                        <span>Transfer money</span>
                      </div>
                      <div className="description">
                        Start transferring money to your child and help them
                        earn, save and spend
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="button-wrapper d-flex justify-content-center">
                <a href="/" className="btn btn-blue">
                  get started
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* Account Setup */}
      <YourOwnCardSection isMobileState={isMobileState}></YourOwnCardSection>
      <PartnersSection
        data={page[1]}
        contentBody={contentBody}
        isMobileState={isMobileState}
      ></PartnersSection>
      <Testimonials data={page[2]} isMobileState={isMobileState}></Testimonials>
      <PricingSection isMobileState={isMobileState}></PricingSection>
      <SocialSection
        data={page[3]}
        isMobileState={isMobileState}
      ></SocialSection>
      <FaqSection data={page[4]} isMobileState={isMobileState}></FaqSection>
      <ChatWithUs isMobileState={isMobileState}></ChatWithUs>
    </div>
  );
};

export default ParentsComponent;
