import React, { createRef, useEffect } from "react";
import "./KeyFeatures.scss";
import KeyFeatureItem from "../key-feature-item/KeyFeatureItem";
import { getImage } from "../../common-service";
import { Fragment } from "react";

const KeyFeatures = (props) => {
  const isMobileState = props.isMobileState;
  const features = [
    {
      title: "Parent App",
      description:
        "Primary account is your control centre from where you monitor and fund their cards",
      url: getImage('feature1.png'),
    },
    {
      title: "Instant Transfer",
      description:
        "Transfer funds instantly to your kids accounts anytime, anywhere",
      url: getImage('feature2.png'),
    },
    {
      title: "Be in Control",
      description:
        "Family-friendly controls and oversight of the children spends in real time",
      url: getImage('feature3.png'),
    },
    {
      title: "Give Rewards",
      description:
        "Encourage good money habits in your children by motivating them to save and earn set rewards on monthly basis",
      url: getImage('feature4.png'),
    },
  ];

  const targetImage = createRef();

  const mouseHandler = (e) => {
    const featuresWrap = document.querySelectorAll('.feature-key-item')
    if (e.type === 'mouseenter') {
      featuresWrap[0].children[0].classList.remove('isActive');
    } else {
      featuresWrap[0].children[0].classList.add('isActive');
      targetImage.current.src = features[0].url;
    }
  }

  const featureWrapperRef = createRef();

  useEffect(() => {
    const targetVar = featureWrapperRef.current;
    targetVar.children[0].children[0].classList.add('isActive')
    return () => {
      targetVar.children[0].children[0].classList.remove('isActive')
    }
  }, [featureWrapperRef])

  const titleFragment = <Fragment>
  <div className="title-wrapper isIcon inline mb-10">
    <span className="text">
      key <b>features</b>
    </span>
    <span className="icon">
      <img src={getImage('green-character.svg')} alt="Muvin" />
    </span>
  </div>

  </Fragment>

  return (
    <div className="key-features-wrapper">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="content-container">
              {!isMobileState && titleFragment}
            </div>
          </div>
          <div className="col-12 col-md-5">
            <div className="phone-wrapper key-features">
              <img
                src={features[0].url}
                alt="Muvin"
                className="img-fluid"
                ref={targetImage}
              />
            </div>
          </div>
          <div className="col-12 col-md-7">
            <div className="content-container">
              {isMobileState && titleFragment}
              <div
                className="key-feature-conainer"
                onMouseEnter={mouseHandler}
                onMouseLeave={mouseHandler}
                ref={featureWrapperRef}
              >
                {features.map((feature, idx) => (
                  <KeyFeatureItem
                    key={"keyFeature" + (idx + 1)}
                    data={feature}
                    target={targetImage}
                  ></KeyFeatureItem>
                ))}
              </div>
              <div className="button-wrapper">
                <a href="/" className="btn btn-blue">
                  explore more features
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default KeyFeatures;
