import React from "react";
import "./PartnersSection.scss";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import ReactHtmlParser from "react-html-parser";

const PartnersSection = (props) => {
  const partnersImg = props.data.imagesCollection.items[0].url;
  const partnersContent = props.contentBody;
  const isMobileState = props.isMobileState;
  return (
    <div className="partners-wrapper">
      <div className="container">
        <div className="row">
          <div
            className={
              "col-12 col-md-6 " + (isMobileState ? "order-2" : "order-1")
            }
          >
            <div className="content-container">
              <div className="title-wrapper isIcon right">
                <span className="text">
                  {ReactHtmlParser(partnersContent.heading)}
                </span>
              </div>
              {partnersContent.description !== null ? (
                <div className="content-wrapper">
                  {partnersContent.description}
                </div>
              ) : (
                ""
              )}
              {partnersContent.linkWrapper.isLink ? (
                <div
                  className={
                    "button-wrapper " + (isMobileState ? "mt-24" : "order-2")
                  }
                >
                  <a
                    href={partnersContent.linkWrapper.linkHref}
                    className="btn btn-blue"
                  >
                    {partnersContent.linkWrapper.linkText}
                  </a>
                </div>
              ) : (
                ""
              )}
            </div>
          </div>
          <div
            className={
              "col-12 col-md-6 " + (isMobileState ? "order-1" : "order-2")
            }
          >
            <div className="phone-wrapper" id="partner-image">
              <LazyLoadImage effect="opacity" src={partnersImg} alt="Muvin" className="img-fluid" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PartnersSection;
