import React from "react";
import "./ParentsSection.scss";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { Link } from "react-router-dom";
import { getImage } from "../../common-service";

const ParentsSection = (props) => {
  const isMobileState = props.isMobileState;

  return (
    <div className="parents-wrapper">
      <div className="container">
        <div className="row">
          <div
            className={
              "col-12 col-md-7 " + (isMobileState ? "order-2" : "order-1")
            }
          >
            <div className="content-container">
              <div className="title-wrapper isIcon top">
                <span className="icon hidden-sm">
                  <LazyLoadImage effect="opacity" src={getImage("purple-character.png")} alt="Muvin" />
                </span>
                <span className="text">
                  imagine your child grows up to be <b>financially free</b>
                </span>
              </div>
              <div className="content-wrapper">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
                enim ad minim veniam, quis nostrud exercitation ullamco laboris
                nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor
                in reprehenderit in voluptate velit esse cillum dolore eu fugiat
                nulla pariatur.
              </div>
              <div className="button-wrapper">
                <Link to="/parents" className="btn btn-blue">
                  know more
                </Link>
              </div>
            </div>
          </div>
          <div
            className={
              "col-12 col-md-5 " + (isMobileState ? "order-1" : "order-2")
            }
          >
            <div className="phone-wrapper">
              <LazyLoadImage effect="opacity" src={isMobileState ? getImage("image-your-child-grow-mobile.png") : getImage("phone.svg")} alt="Muvin" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ParentsSection;
