import React, { useState, useEffect } from "react";
import "./OurStoryPage.scss";
import { LazyLoadImage } from "react-lazy-load-image-component";
import Banner from "../banner/Banner";
import Careers from "../careers/Careers";
import ChatWithUs from "../common/chat-with-us/ChatWithUs";
import Loader from "../common/loader/Loader";
import PartnersSection from "../partners-section/PartnersSection";
import TeamSection from "../team-section/TeamSection";
import { InFocusSection } from "../in-focus-section/InFocusSection";

import { getData, getImage, getStaticDataFunc, getWindowWidth, setStaticDataFunc } from '../../common-service';

const query = `{
  page(id: "12DeLXOJKLmAXo6qHdQN4C") {
    assetsCollection {
      items {
        fileName
        url
      }
    }
    articlesCollection(limit: 50) {
      items {
        ... on Section {
          sectionLabel
          titleMain
          titleSub
          description
          imagesMobileCollection(limit: 50) {
            items {
              url
            }
          }
          imagesCollection(limit: 50) {
            items {
              url
            }
          }
          cardsCollection(limit: 30) {
            items {
              ... on InFocus {
                thumbnail {
                  url
                }
                sourceThumbnail {
                  url
                }
                title
                description
                articleLink
              }
              ... on Career {
                position
                vertical
                location
                jobType
                department
                description
              }
            }
          }
        }
      }
    }
  }
}
`;

