import React, { useContext } from "react";
import "./CareersInfoSection.scss";
import {
  Accordion,
  AccordionContext,
  Card,
  useAccordionToggle,
} from "react-bootstrap";
import accordionTriggerDown from "../../assets/images/accordion-trigger-down.png";
import { Tab, Nav } from "react-bootstrap";
import { getImage } from "../../common-service";

const CareersInfoDetails = (props) => {
  const postList = [];
  const postDetails = props.data.cardsCollection.items;
  const isMobileState = props.isMobileState;

  props.list.verticals.map((a) => {
    const data = {
      name: a,
      counter: 0,
    };
    return postList.push(data);
  });

  postList.forEach((pl) => {
    postDetails.forEach((pd) => {
      if (pl.name === pd.vertical) {
        pl.counter++;
      }
    });
  });
  function ContextAwareToggle({ children, eventKey, callback }) {
    const currentEventKey = useContext(AccordionContext);

    const decoratedOnClick = useAccordionToggle(
      eventKey,
      () => callback && callback(eventKey)
    );

    const isCurrentEventKey = currentEventKey === eventKey;

    return (
      <div className={"accordion-trigger " + (isCurrentEventKey ? "isOpen" : "")}>
        <img
          src={isMobileState ? getImage('accordion-trigger-mobile.png') : accordionTriggerDown}
          alt="Muvin"
          className="img-fluid"
          onClick={decoratedOnClick}
        />
      </div>
    );
  }
  return (
    <div className="careers-section-wrapper">
      <div className="container">
        <div className="row">
          <Tab.Container
            id="careers-tabs"
            defaultActiveKey={postList[0].name.replace(" ", "")}
          >
            <div className="col-12 col-md-3 pl-sm-0 pr-sm-0 mb-sm-36">
              <Nav className="careers-post-list">
                {postList.map((nav, idx) => (
                  <Nav.Item key={"careersPost" + (idx + 1)}>
                    <Nav.Link
                      eventKey={nav.name.replace(" ", "")}
                      className={nav.counter === 0 ? "disabled" : ""}
                    >
                      {nav.name}
                    </Nav.Link>
                  </Nav.Item>
                ))}
              </Nav>
            </div>
            <div className="col-12 col-md-9">
              <Tab.Content>
                {postList.map((nav, idx) => (
                  <Tab.Pane key={"careerPan" + (idx + 1)} eventKey={nav.name.replace(" ", "")}>
                    <div className="heading">{nav.name}</div>
                    <Accordion
                      key={"careerPost" + (idx + 1)}
                      defaultActiveKey={isMobileState ? 0 : 1}
                      className="careers-accordion"
                    >
                      {postDetails.map(
                        (post, i) =>
                          nav.name === post.vertical && (
                            <Card key={"careerPost" + (i + 1)}>
                              <Card.Header>
                                <div className="heading">
                                  <div className="text">{post.position}</div>
                                  <div className="location">
                                    {post.location}
                                  </div>
                                </div>
                                <ContextAwareToggle eventKey={i + 1}>
                                  Click me!
                                </ContextAwareToggle>
                                <Accordion.Toggle
                                  as={Card.Link}
                                  eventKey={i + 1}
                                ></Accordion.Toggle>
                              </Card.Header>
                              <Accordion.Collapse eventKey={i + 1}>
                                <Card.Body>
                                  <div className="description-title">
                                    Type: <span>{post.jobType}</span>
                                  </div>
                                  <div className="department">
                                    Department: <span>{post.department}</span>
                                  </div>
                                  <div className="description-heading">
                                    Description: <span>{post.department}</span>
                                  </div>
                                  <div className="description-content">
                                    {post.description}
                                  </div>
                                </Card.Body>
                              </Accordion.Collapse>
                            </Card>
                          )
                      )}
                    </Accordion>
                  </Tab.Pane>
                ))}
              </Tab.Content>
            </div>
          </Tab.Container>
        </div>
      </div>
    </div>
  );
};

export default CareersInfoDetails;
