import React, { createRef, useEffect } from "react";
import "./EducateYourChild.scss";
import EducateYourChildItem from "../educate-your-child-item/EducateYourChildItem";
import { getImage } from "../../common-service";

const EducateYourChild = (props) => {
  const isMobileState= props.isMobileState;
  const features = [
    {
      title: "in-depth video content",
      description: "Schedule transfer to your child every month",
      url: getImage('educate-clip-mobile-image1.png'),
    },
    {
      title: "give rewards",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna.",
      url: getImage('educate-clip-mobile-image1.png'),
    },
    {
      title: "weekly blog",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna.",
      url: getImage('educate-clip-mobile-image1.png'),
    }
  ];
  const clipWrapper = createRef();
  const videoWrapper = createRef();

  const mouseHandler = (e) => {
    const featuresWrap = document.querySelectorAll('.educate-your-child-item')
    if (e.type === 'mouseenter') {
      featuresWrap[0].children[0].classList.remove('isActive');
    } else {
      featuresWrap[0].children[0].classList.add('isActive');
    }
    videoWrapper.current.children[0].src = features[0].url;
    videoWrapper.current.classList.add("isShow");
  }

  const eycWrapperRef = createRef();

  useEffect(() => {
    const targetVar = eycWrapperRef.current;
    targetVar.children[0].children[0].classList.add('isActive')
    videoWrapper.current.children[0].src = features[0].url;
    videoWrapper.current.classList.add("isShow");
    return () => {
      targetVar.children[0].children[0].classList.remove('isActive')
    }
  }, [eycWrapperRef, videoWrapper])

  return (
    <div className="educate-your-child-wrapper">
      <div className="container">
        <div className="row">
          <div
            className={"col-12 col-md-6 " + (isMobileState ? "order-3" : "")}
          >
            <div className="content-container">
              <div className="title-wrapper isIcon inline mb-10">
                <span className="text">
                  educate <br />
                  <b>your child</b>
                </span>
              </div>
              <div className="educate-your-child-conainer"
                onMouseEnter={mouseHandler}
                onMouseLeave={mouseHandler}
                ref={eycWrapperRef}
              >
                {features.map((items, idx) => (
                  <EducateYourChildItem
                    key={"knowYourChild" + (idx + 1)}
                    clip={items}
                    index={idx}
                    scrollTarget={clipWrapper}
                    videoTarget={videoWrapper}
                  ></EducateYourChildItem>
                ))}
              </div>
            </div>
          </div>
          <div
            className={
              "col-12 col-md-6 col-lg-4 offset-lg-2 " + (isMobileState ? "order-1" : "")
            }
          >
            <div className="phone-wrapper educate-your-child">
              <div className="phone-frame">
                <img src={getImage('samsung-phone-wrapper.png')} alt="Muvin" className="img-fluid" />
                <div className="phone-content-wrapper">
                  <div className="heading">Videos</div>
                  <div className="clip-body">
                    <div className="clip-wrapper" ref={clipWrapper}>
                      {features.map((clip, idx) => (
                        <div
                          key={"clipWrapper" + (idx + 1)}
                          className="clip-item"
                          data-source={idx}
                        >
                          <div className="image">
                            <img
                              src={clip.url}
                              alt="Muvin"
                              className="img-fluid"
                            />
                          </div>
                          <div className="title">{clip.title}</div>
                          <div className="time">10 minute</div>
                        </div>
                      ))}
                    </div>
                  </div>
                  <div className="clip-video-wrapper" ref={videoWrapper}>
                    <img src="" alt="" />
                  </div>
                </div>
              </div>
              <div className="phone-character">
                <img src={getImage('red-character1.svg')} alt="Muvin" />
                <div className="title"></div>
              </div>
            </div>
          </div>
          {isMobileState ? (
            <div className="col-12 order-4">
              <div className="content-container">
                <div className="button-wrapper">
                  <a href="/" className="btn btn-blue">
                    explore more features
                  </a>
                </div>
              </div>
            </div>
          ) : (
            ""
          )}
        </div>
      </div>
    </div>
  );
};

export default EducateYourChild;
